<template>
  <div class="tickets-block">
    <div class="ticket">
      <div class="logo">
        <img src="../assets/emblem.png" alt="">
        <div class="info">
          <p>{{getState.form.city}}</p>
          <p>КП ММР {{getState.form.city}}електротранс</p>
          <p>121961448</p>
        </div>
      </div>
      <div class="img-price">
        <img src="../assets/money5.png" alt="" v-if="getState.form.quantity == 1">
      </div>
      <div class="carriage">
        Вагон № <span>{{getState.form.carriage}}</span>
      </div>
      <div class="ticket-info">
        <div class="item">
          <span>Дата</span>
          <span class="main">{{todayDate}}</span>
        </div>
        <div class="item">
          <span>Время</span>
          <span class="main">{{getState.time}}</span>
        </div>
        <div class="item">
          <span>Стандартный</span>
          <span class="main">{{getState.form.quantity}}</span>
        </div>
      </div>
      <div class="timer">
        00:<span>{{minutes}}</span>:<span>{{seconds}}</span>
      </div>
    </div>
    <button class="compost" :class="{show: !getState.activeCompost}" @click="activate()">Компостировать</button>
  </div>
</template>

<script>

export default {
  data () {
    return {
      time: ''
    }
  },
  computed: {
    getState() {
      return this.$store.state;
    },
    todayDate() {
      return this.$store.getters.getDate;
    },
    seconds() {
      return this.$store.getters.secondsShown;
    },
    minutes() {
      return this.$store.getters.minutesShown;
    }
  },
  created() {
    this.$store.commit('getTime');
  },
  methods: {
    activate() {
      this.getState.activeCompost = !this.getState.activeCompost;
      this.$store.commit('toggleTimer');
      this.$store.commit('getTime');
      this.$store.dispatch('repeatCallback');
    }
  }
}
</script>

<style scoped>
.ticket {
  width: 96%;
  margin: auto;
  background-color: rgba(255,255,255, 0.05);
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box;
}
.logo {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.logo img {
  max-width: 55px;
  height: auto;
  margin-right: 10px;
}
.logo p {
  font-size: 11px;
  color: #fff;
  letter-spacing: 0.7px;
  margin-bottom: 4px;
  margin-top: 0;
}
.carriage {
  text-align: center;
  color: rgba(255,255,255, 0.3);
  font-size: 14px;
  margin-bottom: 15px;
}
.carriage span {
  font-size: 24px;
  font-weight: 500;
  position: relative;
  top: 4px;
  color: #fff;
}
.ticket-info {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: stretch;
}
.ticket-info .item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ticket-info span {
  font-size: 12px;
  color: rgba(255,255,255, 0.3);
  text-align: center;
  margin-bottom: 3px;
}
.ticket-info span.main {
  color: #fff;
  font-weight: 500;
  font-size: 15px;
  font-family: 'Verdana';
  letter-spacing: -0.4px;
}
.timer {
  text-align: center;
  color: #71af3e;
  font-size: 18px;
  font-weight: 500;
  margin-top: 30px;
  font-family: 'Verdana';
  letter-spacing: -0.4px;
}

.img-price {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}
.img-price img {
  width: 50%;
}

button.compost {
  width: 96%;
  display: block;
  margin: 15px auto 0;
  background-color: #71af3e;
  text-align: center;
  border-radius: 5px;
  padding: 14px 0;
  border: 0;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.4s ease;
  opacity: 0;
  visibility: hidden;
}
button.compost.show {
  opacity: 1;
  visibility: visible;
}
</style>



