<template>
  <div class="editor-block">
    <form action="" @submit.prevent="edit">
      <div class="input-wrapper">
        <span>Город</span>
        <input type="text" v-model="getState.form.city" placeholder="Ваш город">
      </div>
      <div class="input-wrapper">
        <span>Количество билетов</span>
        <input type="text" v-model="getState.form.quantity" placeholder="Количество">
      </div>
      <div class="input-wrapper">
        <span>Номер вагона</span>
        <input type="text" v-model="getState.form.carriage" placeholder="Номер вагона">
      </div>
      <div class="input-wrapper">
        <span>Начать таймер</span>
        <input type="text" v-model="getState.timer.minutes" placeholder="Время">
      </div>
      <div class="card">
        <div class="title">
          *9999 Карта для выплат
        </div>
        <div class="money">
          {{getState.money}}<span>грн</span>
        </div>
      </div>
      <button type="submit">Оплатить</button>
    </form>
  </div>
</template>

<script>



export default {
  data () {
    return {

    }
  },
  methods: {
    edit() {
      this.$store.state.money -= ( Number(this.getState.form.quantity) * 5 );      
      this.$router.push('/tr/ticket');
    }
  },
  computed: {
    getState() {
      return this.$store.state;
    }
  }
}
</script>


<style scoped>
form {
  width: 94%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
}
form input {
  width: 100%;
  padding: 0;
  margin: 0;
  outline: none;
  background-color: transparent;
  border: 1px solid rgba(255,255,255, 0.2);
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
  box-sizing: border-box;
  font-size: 14px;
  color: #fff;
}
form .input-wrapper {
  width: 48%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
form .input-wrapper span {
  font-size: 12px;
  color: rgba(255,255,255,0.3);
  margin-bottom: 5px;
}
.card {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.card .title {
  font-size: 16px;
}
.card .money span{
  margin-left: 3px;
  font-size: 12px;
}
form button {
  width: 100%;
  background-color: #71af3e;
  text-align: center;
  border-radius: 5px;
  padding: 12px 0;
  border: 0;
  font-size: 16px;
  color: #fff;
  margin-top: 25px;
  cursor: pointer;
}
</style>



