import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        form: {
            city: 'Миколаїв',
            quantity: '1',
            carriage: '1111'
            
        },
        timer: {
            minutes: 60,
            seconds: 0,
            started: false
        },
        money: 14396,
        activeCompost: false,
        time: ''
    },
    getters: {
        getDate() {
            function zeroPadding(num, digit) {
                var zero = '';
                for(var i = 0; i < digit; i++) {
                    zero += '0';
                }
                return (zero + num).slice(-digit);
            }
            let cd = new Date();
            var date = zeroPadding(cd.getDate(), 2) + '.' + zeroPadding(cd.getMonth()+1, 2) + '.' + zeroPadding(cd.getFullYear(), 4);
            return date;
        },
        createdTime() {
            function zeroPadding(num, digit) {
                var zero = '';
                for(var i = 0; i < digit; i++) {
                    zero += '0';
                }
                return (zero + num).slice(-digit);
            }
            let today = new Date();
            var time = zeroPadding(today.getHours(), 2) + ":" + zeroPadding(today.getMinutes(), 2) + ":" + zeroPadding(today.getSeconds(), 2);
            return time;
        },
        secondsShown(state) {
            if(state.timer.seconds < 10) {
              return "0" + parseInt(state.timer.seconds, 10)
            }
            return state.timer.seconds
        },
        minutesShown(state) {
            if(state.timer.minutes < 10) {
              return "0" + parseInt(state.timer.minutes, 10)
            }
            return state.timer.minutes
        }
    },
    mutations: {
        getTime(state) {
            function zeroPadding(num, digit) {
                var zero = '';
                for(var i = 0; i < digit; i++) {
                    zero += '0';
                }
                return (zero + num).slice(-digit);
            }
            let today = new Date();
            state.time = zeroPadding(today.getHours(), 2) + ":" + zeroPadding(today.getMinutes(), 2) + ":" + zeroPadding(today.getSeconds(), 2);
        },
        toggleTimer(state) {
            if(state.timer.started) {
                state.timer.started = false
            } else {
            //   if(!state.timer.minutes && !state.timer.seconds)
                // this.resetTimer()
                state.timer.started = true
            }
        },
        intervalCallback(state) {
            if(!state.timer.started) return false
            if(state.timer.seconds == 0) {
              if(state.timer.minutes == 0) {
                // this.timerComplete()
                return null
              }
              state.timer.seconds = 59
              state.timer.minutes--
            } else {
                state.timer.seconds--
            }
          }
    },
    actions: {
        repeatCallback({commit}, payload) {
            setInterval(() => {
                commit('intervalCallback');
            }, 1000);
        }
    }


})


