import Vue from 'vue'
import Router from 'vue-router'
import Editor from '@/components/Editor'
import Ticket from '@/components/Tickets'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/tr',
      name: 'Editor',
      component: Editor
    },
    {
      path: '/tr/ticket',
      name: 'Ticket',
      component: Ticket
    }
  ],
  mode: 'history'
})



