<template>
  <header>
      <div class="title">
          <router-link class="arrow" to="/tr">
              <img src="../assets/arrow.svg" alt="">
          </router-link>
          <span>{{title}}</span>
      </div>
      <div class="tickets-info">
          <div class="ticket-button" :class="{active:enable}" @click="enable = !enable">Билет</div>
          <div class="ticket-button" :class="{active:!enable}" @click="enable = !enable">Проездной</div>
      </div>
  </header>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      title: 'Городской транспорт',
      enable: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
header {
    margin-bottom: 10px;
}
.title {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin-bottom: 30px;
}
.title span {
    color: #fff;
    font-size: 15px;
    font-weight: bold;
}
.title .arrow {
    position: absolute;
    left: 10px;
    height: 20px;
    width: 20px;
    cursor: pointer;
}
.title .arrow img {
    width: 100%;
    height: 100%;
}

.tickets-info {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(255,255,255, 0.1);
}
.tickets-info .ticket-button {
    width: 50%;
    text-align: center;
    font-size: 17px;
    color: rgba(255,255,255, 0.7);
    padding-bottom: 10px;
    cursor: pointer;
    border-bottom: 3px solid transparent;
    transition: all 0.4s ease;
}
.tickets-info .ticket-button.active {
    color: #71af3e;
    border-bottom: 3px solid #71af3e;
} 
</style>



